import React, { Component, Fragment } from 'react';
import Page from 'components/LayoutComponents/Page';
import { BtnSave } from 'components/common/button';
import { Card, Row, Table, Col, Button } from 'antd';
import { phanlop } from 'dump/data';
import FormSerchStudent from './FormSerchStudent';
import FormSearchClass from './FormSearchClass';


const columns = [
  {
    title: 'Thao tc',
    dataIndex: 'thaotac',
    render: () => (
      <Fragment>
        <Button type="primary">Chn</Button>
      </Fragment>
    ),
  },
  {
    title: 'S BD',
    dataIndex: 'sbd',
    key: 'name',
  },
  {
    title: 'H v tn',
    dataIndex: 'name',
    key: 'nameEn',
  },
  {
    title: 'Ngy sinh',
    dataIndex: 'birthdate',
    key: 'fromScores',
  },
];

const columnsTableChooes = [
  {
    title: 'Thao tc',
    dataIndex: 'thaotac',
    render: () => (
      <Fragment>
        <Button type="danger">Xa</Button>
      </Fragment>
    ),
  },
  {
    title: 'S BD',
    dataIndex: 'sbd',
    key: 'name',
  },
  {
    title: 'H v tn',
    dataIndex: 'name',
    key: 'nameEn',
  },
  {
    title: 'Ngy sinh',
    dataIndex: 'birthdate',
    key: 'fromScores',
  },
];

class ClassDivisionPage extends Component {
  state = {};
  render() {
    return (
      <Fragment>
        <Card title="Phn lp SV">
          <Row>
            <Col span={24}>
              <FormSerchStudent />
            </Col>
          </Row>
          <Row>
            <Col span={24}>
              <Table bordered dataSource={phanlop} columns={columns} />
            </Col>
          </Row>
        </Card>
        <Card title="Danh sch  chn">
          <Row>
            <Col span={24}>
              <FormSearchClass />
            </Col>
            <Col span={24}>
              <Table bordered dataSource={phanlop} columns={columnsTableChooes} />
            </Col>
            <Col offset={8} span={12}>
              <BtnSave htmlType="submit" />
            </Col>
          </Row>
        </Card>
      </Fragment>
    );
  }
}

export default props => (
  <Page {...props}>
    <ClassDivisionPage />
  </Page>
);



