import React, { Component } from 'react';
import { Row, Col, Form, Input, } from 'antd';
import { BtnSearch, BtnClear } from 'components/common/button';


@Form.create({ name: 'formSerchIndividual' })
class FormSerchIndividual extends Component {
  state = {};
  handleReset = () => {
    this.props.form.resetFields();
  };
  handleSearch = e => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      console.log('Received values of form: ', values);
    });
  };
  render() {
    const { getFieldDecorator } = this.props.form;
    return (
      <Form layout="vertical" onSubmit={this.handleSearch}>
        <Row gutter={24}>
          <Col span={8}>
            <Form.Item label="S BD">{getFieldDecorator('sobd', {})(<Input />)}</Form.Item>
          </Col>
          <Col span={8}>
            <Form.Item label="Tn">{getFieldDecorator('name', {})(<Input />)}</Form.Item>
          </Col>
          <Col span={8}>
            <Form.Item label="Gii tnh">{getFieldDecorator('sex', {})(<Input />)}</Form.Item>
          </Col>
          <Col span={8}>
            <Form.Item label="Ngnh TS">{getFieldDecorator('nganhTS', {})(<Input />)}</Form.Item>
          </Col>
          <Col span={8}>
            <Form.Item label="Tng im">{getFieldDecorator('tongdiem', {})(<Input />)}</Form.Item>
          </Col>
        </Row>
        <Row>
          <Col span={24} style={{ textAlign: 'right' }}>
            <div className="btn-group">
              <BtnSearch />
              <BtnClear />
            </div>
          </Col>
        </Row>
      </Form>
    );
  }
}

export default FormSerchIndividual;



